<Card
  key={plan.name}
  onClick={() => setSelectedPlan(plan.name)}
  className={`cursor-pointer flex flex-col h-full border-2 transition-all 
    ${
      selectedPlan === plan.name
        ? "border-blue-500 shadow-md"
        : "border-gray-200"
    }`}
>
  {/* Card Header */}
  <CardHeader>
    <div className="flex justify-between items-center">
      <CardTitle className="text-2xl">{plan.name}</CardTitle>

      {plan.popular && (
        <Badge className="bg-blue-600 text-white">Popular</Badge>
      )}
    </div>

    <p className="text-blue-600 text-3xl font-bold mt-2">{plan.price}</p>
    <p className="text-gray-500">{plan.description}</p>
  </CardHeader>

  {/* Content expands to fill height */}
  <CardContent className="flex-1 mt-4 space-y-2">
    {plan.features.map((feat) => (
      <div
        key={feat}
        className="flex items-center gap-2 text-gray-700"
      >
        <span className="text-green-500">âœ”</span>
        <span>{feat}</span>
      </div>
    ))}
  </CardContent>

  {/* Button ALWAYS at bottom */}
  <CardFooter className="mt-auto">
    <Button
      className="w-full"
      variant={selectedPlan === plan.name ? "default" : "outline"}
    >
      {selectedPlan === plan.name ? "Selected" : "Choose"}
    </Button>
  </CardFooter>
</Card>
